nav.menu {
  display: flex;
}

div.container {
  display: flex;
  flex-direction: column;
  margin: 40px 0 0;
  padding: 0 8px 0 0;
  overflow-y: auto;
  scrollbar-gutter: stable;
  scrollbar-color: #8585ad #2f2f37;
}

div.container::-webkit-scrollbar {
  width: 8px;
}

div.container::-webkit-scrollbar-track {
  background: #2f2f37;
  margin-bottom: 52px;
}

div.container::-webkit-scrollbar-thumb {
  background: #8585ad;
}
